.reservation-item-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 40px;
  border: 1px solid lightgrey;
  height: 150px;
  border-radius: 5px;
  box-shadow: 0 7px 4px rgba(0, 0.2, 0.1, 0.2);
  .reservation-item__info {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .seat__num {
    color: red;
    font-weight: bolder;
    padding-left: 10px;
  }
}
